/* button login & signup */
.button {
  display: inline-block;
  background-color: #000000;
  border-radius: 10px;
  border: 4px double #cccccc;
  color: #e4e4e4;
  text-align: center;
  font-size: 20px;
  padding: 3px;
  width: 100px;
  transition: all 0.5s;
  cursor: pointer;
  margin: 5px;
}
button span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}
.button span:after {
  content: '\BB';
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}
.button:hover {
  background-color: #0004f1;
}
.button:hover span {
  padding-right: 25px;
}
.button:hover span:after {
  opacity: 1;
  right: 0;
}




